'use client'

import { useState } from 'react'
import { AdminLayout } from '@/components/layout/admin-layout'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table'
import { CreateScheduleEventDialog } from '@/components/schedule/create-schedule-event-dialog'
import { 
  CalendarCheck, 
  Plus, 
  Search,
  Clock,
  MapPin,
  Users as UsersIcon,
  Filter,
  Calendar as CalendarIcon
} from 'lucide-react'
import { 
  mockScheduleEvents,
  getTodayEvents,
  getUpcomingEvents,
  type ScheduleEvent,
  type ScheduleEventType,
  type ScheduleEventStatus
} from '@/lib/mock/schedule-events'
import { mockUsers } from '@/lib/mock-data'
import { formatDateWithWeekday, formatTime } from '@/lib/utils'
import { format } from 'date-fns'

const EVENT_TYPE_LABELS: Record<ScheduleEventType, string> = {
  'meeting': '미팅',
  'conference': '회의',
  'appointment': '약속',
  'vacation': '휴가',
  'training': '교육',
  'other': '기타',
}

const EVENT_STATUS_LABELS: Record<ScheduleEventStatus, string> = {
  'scheduled': '예정',
  'confirmed': '확정',
  'cancelled': '취소',
  'completed': '완료',
}

const EVENT_TYPE_COLORS: Record<ScheduleEventType, string> = {
  'meeting': 'bg-blue-100 text-blue-800',
  'conference': 'bg-purple-100 text-purple-800',
  'appointment': 'bg-green-100 text-green-800',
  'vacation': 'bg-orange-100 text-orange-800',
  'training': 'bg-pink-100 text-pink-800',
  'other': 'bg-gray-100 text-gray-800',
}

const EVENT_STATUS_COLORS: Record<ScheduleEventStatus, string> = {
  'scheduled': 'bg-yellow-100 text-yellow-800',
  'confirmed': 'bg-green-100 text-green-800',
  'cancelled': 'bg-red-100 text-red-800',
  'completed': 'bg-gray-100 text-gray-800',
}

export default function SchedulePage() {
  const [searchQuery, setSearchQuery] = useState('')
  const [selectedTab, setSelectedTab] = useState<'all' | 'today' | 'upcoming'>('upcoming')
  const [createDialogOpen, setCreateDialogOpen] = useState(false)
  
  // Get events based on tab
  const getFilteredEvents = () => {
    let events: ScheduleEvent[] = []
    
    switch (selectedTab) {
      case 'today':
        events = getTodayEvents()
        break
      case 'upcoming':
        events = getUpcomingEvents()
        break
      default:
        events = [...mockScheduleEvents].sort((a, b) => {
          const dateCompare = b.date.localeCompare(a.date)
          if (dateCompare !== 0) return dateCompare
          return b.startTime.localeCompare(a.startTime)
        })
    }
    
    // Apply search filter
    if (searchQuery) {
      const query = searchQuery.toLowerCase()
      events = events.filter(event => 
        event.title.toLowerCase().includes(query) ||
        event.location?.toLowerCase().includes(query) ||
        event.description?.toLowerCase().includes(query)
      )
    }
    
    return events
  }
  
  const filteredEvents = getFilteredEvents()
  
  // Statistics
  const todayCount = getTodayEvents().length
  const upcomingCount = getUpcomingEvents().length
  const confirmedCount = mockScheduleEvents.filter(e => e.status === 'confirmed').length
  
  const getUserName = (userId: string) => {
    const user = mockUsers.find(u => u.id === userId)
    return user ? `${user.lastName}${user.firstName}` : '알 수 없음'
  }

  return (
    <AdminLayout align="left">
      <div className="space-y-6">
        {/* Header */}
        <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4">
          <div>
            <h1 className="text-3xl font-bold tracking-tight flex items-center gap-2">
              <CalendarCheck className="h-8 w-8" />
              일정 관리
            </h1>
            <p className="text-muted-foreground mt-1">
              미팅, 회의, 교육 등 일반 일정을 관리하세요
            </p>
          </div>
          <Button 
            className="w-full sm:w-auto"
            onClick={() => setCreateDialogOpen(true)}
          >
            <Plus className="mr-2 h-4 w-4" />
            새 일정 등록
          </Button>
        </div>

        {/* KPI Cards */}
        <div className="grid gap-4 md:grid-cols-4">
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">오늘 일정</CardTitle>
              <CalendarIcon className="h-4 w-4 text-muted-foreground" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{todayCount}</div>
              <p className="text-xs text-muted-foreground">
                오늘 예정된 일정
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">예정된 일정</CardTitle>
              <Clock className="h-4 w-4 text-blue-600" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-blue-600">{upcomingCount}</div>
              <p className="text-xs text-muted-foreground">
                다가오는 일정
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">확정된 일정</CardTitle>
              <CalendarCheck className="h-4 w-4 text-green-600" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-green-600">{confirmedCount}</div>
              <p className="text-xs text-muted-foreground">
                확정 완료
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">전체 일정</CardTitle>
              <Filter className="h-4 w-4 text-muted-foreground" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{mockScheduleEvents.length}</div>
              <p className="text-xs text-muted-foreground">
                총 등록된 일정
              </p>
            </CardContent>
          </Card>
        </div>

        {/* Search */}
        <div className="flex gap-4">
          <div className="relative flex-1">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
            <Input
              placeholder="일정 검색..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="pl-9"
            />
          </div>
        </div>

        {/* Tabs */}
        <Tabs value={selectedTab} onValueChange={(v) => setSelectedTab(v as any)}>
          <TabsList>
            <TabsTrigger value="upcoming">예정된 일정</TabsTrigger>
            <TabsTrigger value="today">오늘</TabsTrigger>
            <TabsTrigger value="all">전체</TabsTrigger>
          </TabsList>

          <TabsContent value={selectedTab} className="mt-6">
            {filteredEvents.length === 0 ? (
              <Card>
                <CardContent className="py-8 text-center text-muted-foreground">
                  일정이 없습니다
                </CardContent>
              </Card>
            ) : (
              <Card>
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>일정명</TableHead>
                      <TableHead>유형</TableHead>
                      <TableHead>날짜/시간</TableHead>
                      <TableHead>장소</TableHead>
                      <TableHead>참석자</TableHead>
                      <TableHead>상태</TableHead>
                      <TableHead className="text-right">작업</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {filteredEvents.map((event) => (
                      <TableRow key={event.id}>
                        <TableCell>
                          <div className="font-medium">{event.title}</div>
                          {event.description && (
                            <div className="text-sm text-muted-foreground mt-1">
                              {event.description.length > 50
                                ? `${event.description.substring(0, 50)}...`
                                : event.description}
                            </div>
                          )}
                        </TableCell>
                        <TableCell>
                          <Badge variant="outline" className={EVENT_TYPE_COLORS[event.type]}>
                            {EVENT_TYPE_LABELS[event.type]}
                          </Badge>
                        </TableCell>
                        <TableCell>
                          <div className="flex items-center gap-2">
                            <CalendarIcon className="h-4 w-4 text-muted-foreground" />
                            <div>
                              <div className="text-sm font-medium">
                                {formatDateWithWeekday(event.date)}
                              </div>
                              <div className="text-xs text-muted-foreground">
                                {event.startTime} - {event.endTime}
                              </div>
                            </div>
                          </div>
                        </TableCell>
                        <TableCell>
                          {event.location ? (
                            <div className="flex items-center gap-2">
                              <MapPin className="h-4 w-4 text-muted-foreground" />
                              <span className="text-sm">{event.location}</span>
                            </div>
                          ) : (
                            <span className="text-muted-foreground text-sm">-</span>
                          )}
                        </TableCell>
                        <TableCell>
                          <div className="flex items-center gap-2">
                            <UsersIcon className="h-4 w-4 text-muted-foreground" />
                            <span className="text-sm">{event.attendees.length}명</span>
                          </div>
                          <div className="text-xs text-muted-foreground mt-1">
                            {event.attendees.slice(0, 2).map(getUserName).join(', ')}
                            {event.attendees.length > 2 && ` 외 ${event.attendees.length - 2}명`}
                          </div>
                        </TableCell>
                        <TableCell>
                          <Badge className={EVENT_STATUS_COLORS[event.status]}>
                            {EVENT_STATUS_LABELS[event.status]}
                          </Badge>
                        </TableCell>
                        <TableCell className="text-right">
                          <Button variant="ghost" size="sm">
                            상세보기
                          </Button>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </Card>
            )}
          </TabsContent>
        </Tabs>
      </div>

      {/* Create Schedule Event Dialog */}
      <CreateScheduleEventDialog
        open={createDialogOpen}
        onOpenChange={setCreateDialogOpen}
        onSuccess={() => {
          // TODO: Refresh data after successful creation
          // For now, just close the dialog
        }}
      />
    </AdminLayout>
  )
}

